<template>
  <div class="home">
    <!-- HomeTitle, -->
    <HomeTitle></HomeTitle>
    <ul class="home-cardlist">
      <!-- CardItem, -->
      <CardItem
        v-for="item in personalizeds"
        :key="item.id"
        :item="item"
      ></CardItem>
    </ul>
    <!-- HomeTitle, -->
    <HomeTitle></HomeTitle>

    <ul>
      <!-- SongListItem -->
      <SongListtwo
        v-for="item in newsongs"
        :key="item.id"
        :item="item"
        @change-current-song="
          $emit('change-current-song', $event);
          $emit('change-current-play-list', newsongs);
        "
        :currentSongId="currentSongId"
        :playing="playing"
      ></SongListtwo>
    </ul>
  </div>
</template>

<script>
// @ is an alias to /src
import CardItem from "@/components/CardItem.vue";
import HomeTitle from "@/components/HomeTitle.vue";
import SongListtwo from "@/components/SongListtwo.vue";

export default {
  name: "Home",
  components: {
    CardItem,
    HomeTitle,
    SongListtwo,
  },
  props: {
    currentSongId: {
      type: Number,
    },
    playing: Boolean,
  },
  data: function () {
    return {
      personalizeds: [],
      newsongs: [],
    };
  },
  created: function () {
    // Ajax this
    // 
    this.axios
      .get("http://apis.netstart.cn/music/personalized?limit=6")
      .then((res) => {
        // console.log(this,res);
        this.personalizeds = res.data.result;
      });

    // 
    this.axios
      .get("http://apis.netstart.cn/music/personalized/newsong")
      .then((res) => {
        // console.log(this,res);
        this.newsongs = res.data.result;
      });
  },
};
</script>

<style  lang="less" scoped>
.home {
  padding-top: 130px;
}
.home-cardlist {
  margin-bottom: 40px;
  display: flex;
  flex-wrap: wrap;
}

</style>
